/* HTML5 Reset
---------------------------------------------------------------------------------------------------- */
/* Baseline Normalize
--------------------------------------------- */
/* normalize.css v8.0.0 | MIT License | github.com/necolas/normalize.css */
button,
hr,
input {
  overflow: visible;
}

progress,
sub,
sup {
  vertical-align: baseline;
}

[type=checkbox],
[type=radio],
legend {
  box-sizing: border-box;
  padding: 0;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

h1 {
  font-size: 2em;
  margin: .67em 0;
}

hr {
  box-sizing: content-box;
  height: 0;
}

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
select {
  text-transform: none;
}

[type=button],
[type=reset],
[type=submit],
button {
  -webkit-appearance: button;
}

[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner,
button::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring,
button:-moz-focusring {
  outline: ButtonText dotted 1px;
}

fieldset {
  padding: .35em .75em .625em;
}

legend {
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal;
}

textarea {
  overflow: auto;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

[hidden],
template {
  display: none;
}

/* Box Sizing
--------------------------------------------- */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

/* Float Clearing
--------------------------------------------- */
.author-box::before,
.clearfix::before,
.entry::before,
.entry-content::before,
.footer-widgets::before,
.nav-primary::before,
.nav-secondary::before,
.pagination::before,
.site-container::before,
.site-footer::before,
.site-header::before,
.site-inner::before,
.widget::before,
.wrap::before {
  content: " ";
  display: table;
}

.author-box::after,
.clearfix::after,
.entry::after,
.entry-content::after,
.footer-widgets::after,
.nav-primary::after,
.nav-secondary::after,
.pagination::after,
.site-container::after,
.site-footer::after,
.site-header::after,
.site-inner::after,
.widget::after,
.wrap::after {
  clear: both;
  content: " ";
  display: table;
}

/* Defaults
---------------------------------------------------------------------------------------------------- */
/* Typographical Elements
--------------------------------------------- */
html {
  font-size: 62.5%;
  /* 10px browser default */
}

/* Chrome fix */
body {
  margin: 0;
  color: #111;
  background-color: #fff;
  font-family: "PT Serif", "times new roman", times, serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2;
}

body > div {
  font-size: 16px;
  font-size: 1.6rem;
}

button,
input:focus,
input[type="button"],
input[type="reset"],
input[type="submit"],
textarea:focus,
.button,
.gallery img {
  transition: all 0.2s ease-in-out;
}

a {
  color: #c93;
  text-decoration: none;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
}

a:active,
a:hover,
#primary_menu ul li.current-menu-item a,
#primary_menu ul li a:hover,
#breadcrumbs .goback:hover,
.channel .channel_title a,
.post .entry-title a:hover,
.post .entry-tools a:hover,
.post_list li a.post_title:hover,
#popup_window h3 a,
.entry-content a,
.comment-author a:hover,
.comment-list .pingback a:hover,
.comment-list .trackback a:hover,
.comment-metadata a:hover {
  color: #c93;
}

a:focus,
a:hover {
  color: #333;
  text-decoration: none;
}

p {
  margin: 0 0 25px;
  padding: 0;
}

ol,
ul {
  margin: 0;
  padding: 0;
}

hr {
  border: 0;
  border-collapse: collapse;
  border-top: 1px solid #eee;
  clear: both;
  margin: 30px 0;
}

b,
strong {
  font-weight: 700;
}

blockquote,
cite,
em,
i {
  font-style: italic;
}

mark {
  background: #ddd;
  color: #333;
}

blockquote {
  margin: 30px;
}

blockquote::before {
  content: "\201C";
  display: block;
  font-size: 30px;
  font-size: 3rem;
  height: 0;
  left: -20px;
  position: relative;
  top: -10px;
}

/* Headings
--------------------------------------------- */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "PT Serif", "times new roman", times, serif;
  font-weight: 700;
  line-height: 1.2;
  margin: 0 0 20px;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.7rem;
}

.entry-content h3,
.entry-content h4 {
  font-weight: 600;
}

h3 {
  font-size: 2.4rem;
}

h4 {
  font-size: 2rem;
}

.entry-content h4 {
  margin-top: 40px;
}

h5 {
  font-size: 1.8rem;
}

h6 {
  font-size: 1.6rem;
}

.entry-content h1,
.entry-content h2,
.entry-content h3,
.entry-content h4,
.entry-content h5,
.entry-content h6 {
  margin-bottom: 1.5rem;
  font-weight: bold;
}

.entry-content h1,
.comment-list li h1 {
  font-size: 2.6rem;
}

.entry-content h2,
.comment-list li h2 {
  font-size: 2.4rem;
}

.entry-content h3,
.comment-list li h3 {
  font-size: 2.2rem;
}

.entry-content h4,
.comment-list li h4 {
  font-size: 2rem;
}

.entry-content h5,
.comment-list li h5 {
  font-size: 1.8rem;
}

.entry-content h6,
.comment-list li h6 {
  font-size: 1.6rem;
}

.entry-content pre,
.entry-content table {
  width: 80%;
}

.post .entry-content {
  margin: 25px 0;
}

.single .post .entry-content {
  font-size: 1.6rem;
}

.post .entry-content blockquote,
.comment-list li blockquote {
  color: #000;
  font: normal 2rem "Roboto Slab",Arial;
  margin: 40px 0;
  padding: 0 40px;
  position: relative;
}

.comment-list li blockquote {
  margin: 20px 0;
}

.post .entry-content blockquote:before,
.comment-list li blockquote:before {
  display: block;
  content: "\201C";
  line-height: 1;
  font-size: 6rem;
  position: absolute;
  left: 0;
  top: -0.1em;
  color: #ddd;
  font-family: "Roboto Slab",Georgia, "Times New Roman", Times, serif;
}

.post .entry-content blockquote::after,
.comment-list li blockquote::after {
  bottom: -35px;
  color: #ddd;
  content: '\201D';
  display: block;
  font-family: "Roboto Slab",Georgia, "Times New Roman", Times, serif;
  font-size: 6rem;
  line-height: 1;
  position: absolute;
  right: 0;
}

.single .entry-content blockquote {
  font-size: 2.2rem;
  line-height: 2;
}

/* Objects
--------------------------------------------- */
embed,
iframe,
img,
object,
video,
.wp-caption {
  max-width: 100%;
}

img {
  height: auto;
  vertical-align: top;
}

figure {
  margin: 0;
}

/* Gallery
--------------------------------------------- */
.gallery {
  overflow: hidden;
}

.gallery img {
  border: 1px solid #eee;
  height: auto;
  padding: 4px;
}

.gallery img:focus,
.gallery img:hover {
  border: 1px solid #999;
  outline: none;
}

.gallery-columns-1 .gallery-item {
  width: 100%;
}

.gallery-columns-2 .gallery-item {
  width: 50%;
}

.gallery-columns-3 .gallery-item {
  width: 33%;
}

.gallery-columns-4 .gallery-item {
  width: 25%;
}

.gallery-columns-5 .gallery-item {
  width: 20%;
}

.gallery-columns-6 .gallery-item {
  width: 16.6666%;
}

.gallery-columns-7 .gallery-item {
  width: 14.2857%;
}

.gallery-columns-8 .gallery-item {
  width: 12.5%;
}

.gallery-columns-9 .gallery-item {
  width: 11.1111%;
}

.gallery-columns-2 .gallery-item:nth-child(2n+1),
.gallery-columns-3 .gallery-item:nth-child(3n+1),
.gallery-columns-4 .gallery-item:nth-child(4n+1),
.gallery-columns-5 .gallery-item:nth-child(5n+1),
.gallery-columns-6 .gallery-item:nth-child(6n+1),
.gallery-columns-7 .gallery-item:nth-child(7n+1),
.gallery-columns-8 .gallery-item:nth-child(8n+1),
.gallery-columns-9 .gallery-item:nth-child(9n+1) {
  clear: left;
}

.gallery-item {
  float: left;
  margin: 0 0 30px;
  text-align: center;
}

/* Forms
--------------------------------------------- */
input,
select,
textarea {
  background-color: #fff;
  border: 1px solid #ddd;
  color: #333;
  font-size: 1.8rem;
  font-weight: 400;
  padding: 15px;
  width: 100%;
}

input:focus,
textarea:focus {
  border: 1px solid #999;
  outline: none;
}

input[type="checkbox"],
input[type="image"],
input[type="radio"] {
  width: auto;
}

::-moz-placeholder {
  color: #333;
  opacity: 1;
}

::-webkit-input-placeholder {
  color: #333;
}

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  background-color: #333;
  border: 0;
  color: #fff;
  cursor: pointer;
  font-size: 1.6rem;
  font-weight: 600;
  padding: 15px 30px;
  text-decoration: none;
  white-space: normal;
  width: auto;
}

button:focus, button:hover,
input[type="button"]:focus,
input[type="button"]:hover,
input[type="reset"]:focus,
input[type="reset"]:hover,
input[type="submit"]:focus,
input[type="submit"]:hover {
  color: #c93;
  background-color: #c93;
}

button.small,
input[type="button"].small,
input[type="reset"].small,
input[type="submit"].small {
  padding: 8px 16px;
}

button:focus,
button:hover,
input[type="button"]:focus,
input[type="button"]:hover,
input[type="reset"]:focus,
input[type="reset"]:hover,
input[type="submit"]:focus,
input[type="submit"]:hover,
.button:focus,
.button:hover {
  color: #333;
  border-color: #888;
  outline: 0;
}

.entry-content .button:focus,
.entry-content .button:hover {
  color: #c93;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

.button {
  display: inline-block;
}

a.button.small,
button.small,
input[type="button"].small,
input[type="reset"].small,
input[type="submit"].small {
  padding: 8px 16px;
}

.site-container button:disabled,
.site-container button:disabled:hover,
.site-container input:disabled,
.site-container input:disabled:hover,
.site-container input[type="button"]:disabled,
.site-container input[type="button"]:disabled:hover,
.site-container input[type="reset"]:disabled,
.site-container input[type="reset"]:disabled:hover,
.site-container input[type="submit"]:disabled,
.site-container input[type="submit"]:disabled:hover {
  background-color: #eee;
  border-width: 0;
  color: #777;
  cursor: not-allowed;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button {
  display: none;
}

.button {
  background-color: #333;
  border: 0;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 1.6rem;
  font-weight: 600;
  padding: 15px 30px;
  text-decoration: none;
  white-space: normal;
  width: auto;
}

.button:focus, .button:hover {
  background-color: #c93;
  color: #c93;
}

.entry-content .button:focus,
.entry-content .button:hover {
  color: #c93;
}

/* Tables
--------------------------------------------- */
table {
  border: 1px solid #3333331a;
  border-collapse: separate;
  border-spacing: 0;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  font: 300 13px 'PT Serif',"times new roman",times,serif;
  line-height: 1.8;
  margin: 0 0 2rem;
  table-layout: fixed;
  width: 100%;
  word-break: break-all;
}

table,
th,
td {
  border: 1px solid rgba(51, 51, 51, 0.1);
}

tbody {
  border-bottom: 1px solid #eee;
}

td,
th {
  text-align: left;
}

td:first-child, td:first-child,
th:first-child,
th:first-child {
  padding-left: 0;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

td {
  border-top: 1px solid #eee;
  padding: 6px;
}

th {
  font-weight: 400;
  padding: 0 6px;
}

/* Screen Reader Text
--------------------------------------------- */
.screen-reader-shortcut,
.screen-reader-text,
.screen-reader-text span {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus,
.widget_search input[type="submit"]:focus {
  background: #fff;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #333;
  display: block;
  font-size: 1em;
  font-weight: bold;
  height: auto;
  padding: 15px 23px 14px;
  text-decoration: none;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

.more-link {
  position: relative;
}

/* Skip Links
---------------------------------------------------------------------------------------------------- */
.genesis-skip-link {
  margin: 0;
}

.genesis-skip-link .skip-link-hidden {
  display: none;
  visibility: hidden;
}

.genesis-skip-link li {
  height: 0;
  list-style: none;
  width: 0;
}

/* Display outline on focus */
:focus {
  color: #333;
  outline: #ccc solid 1px;
}

/* Structure and Layout
---------------------------------------------------------------------------------------------------- */
/* Site Container
--------------------------------------------- */
.site-container {
  -webkit-animation: fadein 1s;
  animation: fadein 1s;
  word-wrap: break-word;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Site Inner
--------------------------------------------- */
.site-inner {
  clear: both;
  padding: 54px 0 0;
}

/* Common Classes
---------------------------------------------------------------------------------------------------- */
/* Avatar
--------------------------------------------- */
.avatar {
  border-radius: 50%;
  float: left;
}

.author-box .avatar,
.alignleft .avatar {
  margin-right: 20px;
}

.alignright .avatar {
  margin-left: 20px;
}

.comment .avatar {
  margin: 0 15px 20px 0;
}

/* Genesis
--------------------------------------------- */
.after-entry,
.archive-description,
.author-box {
  margin-bottom: 40px;
}

.after-entry {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
  padding: 20px 30px;
}

.after-entry .widget:last-of-type {
  margin-bottom: 0;
}

.breadcrumb {
  border-bottom: 1px solid #eee;
  font-size: 1.6rem;
  margin-bottom: 40px;
  padding-bottom: 10px;
}

.archive-description p:last-child,
.author-box p:last-child {
  margin-bottom: 0;
}

/* Search Form
--------------------------------------------- */
.search-form {
  overflow: hidden;
}

.entry-content .search-form {
  margin-bottom: 40px;
  width: 50%;
}

.entry-content p.wp-caption-text {
  margin-bottom: 0;
}

.entry-content .wp-audio-shortcode,
.entry-content .wp-playlist,
.entry-content .wp-video {
  margin: 0 0 30px;
}

.post-password-form input[type='submit'],
.search-form input[type='submit'] {
  margin-top: 10px;
}

.widget_search input[type='submit'] {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  width: 1px;
}

/* Titles
--------------------------------------------- */
.archive-description .entry-title,
.archive-title,
.author-box-title {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 10px;
}

.entry-title {
  font-size: 30px;
  font-size: 3rem;
  margin-bottom: 10px;
}

.entry-title a,
.sidebar .widget-title a {
  color: #000;
  text-decoration: none;
}

.entry-title a:focus,
.entry-title a:hover {
  color: #c93;
}

/* WordPress
--------------------------------------------- */
.aligncenter {
  text-align: center;
}

a.aligncenter img {
  display: block;
  margin: 0 auto;
}

a.alignnone {
  display: inline-block;
}

.alignleft {
  float: left;
  text-align: left;
}

.alignright {
  float: right;
  text-align: right;
}

a.alignleft,
a.alignnone,
a.alignright {
  max-width: 100%;
}

img.centered,
.aligncenter {
  display: block;
  margin: 0 auto 30px;
}

img.alignnone,
.alignnone {
  margin-bottom: 15px;
}

a.alignleft,
img.alignleft,
.wp-caption.alignleft {
  margin: 0 20px 20px 0;
}

a.alignright,
img.alignright,
.wp-caption.alignright {
  margin: 0 0 20px 20px;
}

.gallery-caption,
.wp-caption-text {
  font-size: 1.4rem;
  font-weight: 600;
  margin: 0;
  text-align: center;
}

/* ----------------------------------------------------
[_widgets.scss]

1. Topbar & Header
    - 1.1 Setup
    - 1.2 Typography
2. Genesis Recent Posts

------------------------------------------------------*/
/* 1. Widgets
=================================================*/
/* 1.1 Setup
============================*/
.sidebar .widget a,
.sidebar .widget a:visited {
  color: #c93;
}

.widget {
  font: 300 13px 'PT Serif',"times new roman",times,serif;
  height: auto;
  line-height: 1.8em;
  margin: 0 auto 40px;
  overflow: hidden;
  width: 100%;
}

.widget p:last-child,
.widget ul > li:last-of-type {
  margin-bottom: 0;
}

.widget ul {
  margin-left: 0;
  padding-left: 0;
  list-style: none;
}

.widget ul li {
  line-height: 1.5em;
  margin-bottom: 10px;
}

.widget ul > li {
  margin-bottom: 10px;
}

.widget ul > li:last-of-type {
  padding-bottom: 0;
}

.widget ol > li {
  list-style-position: inside;
  list-style-type: none;
  padding-left: 20px;
}

.widget li li {
  border: 0;
  margin: 0 0 0 30px;
  padding: 0;
}

/* 1.2 Typography
============================*/
.widget .widgettitle,
.widgettitle,
.widget-title {
  border-bottom: 1px solid #000;
  border-left: 0;
  border-right: 0;
  border-top: 2px solid #000;
  box-sizing: content-box;
  display: block;
  font-family: "Arial,Helvetica,sans-serif";
  font-weight: 700;
  height: 30px;
  line-height: 30px;
  margin: 0 auto 20px;
  text-align: center;
  text-transform: uppercase;
}

.widget-title,
.widgettitle {
  font-size: 1.5rem;
  font-weight: 700;
}

.widget.widget_recent_entries ul li,
.widget.widget_recent_comments ul li {
  border-bottom: 1px solid #eee;
