/* 1. Site Navigation
=================================================*/
/* 1.1 Menus Setup
============================*/
.genesis-nav-menu {
  clear: both;
  line-height: 1;
  width: 100%;
}

.genesis-nav-menu .menu-item {
  display: inline-block;
  float: none;
  position: relative;
}

.genesis-nav-menu .menu-item:focus, .genesis-nav-menu .menu-item:hover {
  position: relative;
}

.genesis-nav-menu > .menu-bold > a {
  font-weight: 700;
}

.genesis-nav-menu > .menu-highlight > a {
  background-color: #333;
  border-radius: 3px;
  color: #fff;
  font-weight: 600;
  margin-left: 15px;
  padding-left: 20px;
  padding-right: 20px;
}

.genesis-nav-menu > .menu-highlight > a:hover, .genesis-nav-menu > .menu-highlight > a:focus {
  background-color: #c93;
}

.genesis-nav-menu .sub-menu {
  background-color: transparent;
  line-height: 1.8;
  opacity: 0;
  position: absolute;
  top: 0;
  transition: opacity 0.1s ease-in-out;
}

.genesis-nav-menu .sub-menu .sub-menu {
  top: 20px;
}

.genesis-nav-menu .sub-menu li {
  display: block;
  right: calc(11%);
  top: 40px;
}

.genesis-nav-menu .sub-menu li:last-of-type a {
  background: #333;
  border: 1px solid #333;
  box-sizing: content-box;
  height: 20px;
  padding: 5px 0 0;
}

.genesis-nav-menu .sub-menu a {
  border-top: 0;
}

.genesis-nav-menu a {
  color: #333;
  display: block;
  font-size: 15px;
  font-weight: 400;
  outline-offset: -1px;
  padding-bottom: 12px;
  padding-top: 12px;
  text-decoration: none;
}

.genesis-nav-menu a:focus, .genesis-nav-menu a:hover,
.genesis-nav-menu a .current-menu-item > a,
.genesis-nav-menu a .sub-menu .current-menu-item > a:focus,
.genesis-nav-menu a .sub-menu .current-menu-item > a:hover {
  color: #0073e5;
  text-decoration: none;
}

.genesis-nav-menu .sub-menu {
  background-color: transparent;
  clear: both;
  color: #eee;
  display: none;
  left: -9999px;
  margin: 0;
  opacity: 1;
  padding-left: 15px;
  position: static;
  width: 100%;
  z-index: 99;
}

.genesis-nav-menu .sub-menu a {
  background-color: #000;
  font-size: 14px;
  position: relative;
  width: 100%;
  word-wrap: break-word;
}

.genesis-nav-menu .sub-menu a:last-of-type {
  background: transparent;
}

.genesis-nav-menu .menu-item:focus, .genesis-nav-menu .menu-item:hover {
  position: relative;
}

.genesis-nav-menu .menu-item:hover > .sub-menu {
  left: auto;
  opacity: 1;
}

.genesis-nav-menu .menu-item:hover > .sub-menu .sub-menu {
  left: auto;
}

/* 1.2 Responsive Menu
============================*/
.menu .menu-item:focus {
  position: static;
}

.menu .menu-item > a:focus + ul.sub-menu,
.menu .menu-item.sfHover > ul.sub-menu {
  left: auto;
  opacity: 1;
}

.genesis-responsive-menu {
  display: none;
  padding-bottom: 15px;
  position: relative;
}

.genesis-responsive-menu .genesis-nav-menu .sub-menu,
.genesis-responsive-menu .genesis-nav-menu .sub-menu a {
  width: 160px;
}

.menu-toggle {
  background-color: transparent;
  border-width: 0;
  color: #333;
  display: block;
  float: left;
  line-height: 1;
  margin: 10px 0;
  overflow: hidden;
  padding: 15px 0;
  position: fixed;
  text-align: center;
  top: 22.5px;
  visibility: visible;
  z-index: 1000;
}

.menu-toggle:focus, .menu-toggle:hover {
  background-color: transparent;
  border-width: 0;
  color: #c93;
}

.menu-toggle.activated::before {
  content: '\f335';
}

.sub-menu-toggle {
  background-color: transparent;
  border-width: 0;
  color: #ccc;
  display: block;
  float: right;
  margin: 0 auto;
  overflow: hidden;
  padding: 9px 10px;
  position: absolute;
  right: 0;
  text-align: center;
  top: 0;
  visibility: visible;
  z-index: 100;
}

.sub-menu-toggle:focus, .sub-menu-toggle:hover {
  background-color: transparent;
  border-width: 0;
  color: #c93;
}

.sub-menu-toggle::before {
  display: inline-block;
  text-rendering: auto;
  transform: rotate(0);
  transition: transform .25s ease-in-out;
}

.sub-menu-toggle.activated::before {
  transform: rotate(180deg);
}

.site-header .menu-toggle::before {
  float: left;
  margin-right: 5px;
  position: relative;
  text-rendering: auto;
  top: 1px;
}

.sub-menu .sub-menu-toggle {
  padding: 12px 10px;
}

/* 2. Header Navigation
=================================================*/
/* 2.1 Header Menu
============================*/
.nav-primary {
  border-color: #333;
  border-style: solid;
  border-width: 2px 0 1px;
  box-sizing: content-box;
  clear: both;
  display: none;
  height: 45px;
