  text-transform: uppercase;
  transition: all .1s linear;
}

.nav-primary ul li a::after {
  color: #111;
  font-family: "PT Serif", "times new roman", times, serif;
  font-size: 13px;
}

.nav-primary ul li a:focus {
  outline: 2px solid #c93;
}

