.nav-primary ul li a:hover,
.nav-primary ul li a:active,
.nav-primary ul li a:focus {
  color: #c93;
  text-decoration: none;
}

.nav-primary ul li:last-of-type a {
  background: #333;
  border: 5px solid #000;
  border-radius: 10%;
  color: #fff;
  font-size: 14px;
}

.nav-primary ul .sub-menu li {
  background: #000;
  border-bottom: 1px solid #000;
  padding: 10px 31px;
  width: 100%;
}

.nav-primary li > ul a::after,
.nav-primary ul li:last-of-type a::after {
  border-bottom: 0;
  content: '' !important;
  margin-left: 0 !important;
  width: 100%;
}

.nav-primary .sub-menu a::after {
  border-bottom: 1px solid #333;
  display: block;
  position: relative;
  top: 10.8px;
}

.nav-primary ul.sub-menu li a,
.nav-primary ul.sub-menu li:first-of-type a {
  color: #eee;
  font-size: 12px;
}

/* 2.2 Header Menu Shrinked
============================*/
.nav-shrinked {
  border: 0;
  display: block;
  margin: 1px 0 0;
  min-width: 200px;
  padding: 0;
  position: fixed !important;
  right: 20px;
  width: auto;
}

.nav-shrinked a:after {
  margin: 0 0 0 10px;
}

.nav-shrinked ul {
  font-size: 13px;
}

.nav-shrinked ul li {
  padding: 8px 7px;
}

.nav-shrinked ul li a,
.nav-shrinked ul li:last-of-type a {
  font-size: 12px;
}

.nav-shrinked ul li:last-of-type a {
  background: #333;
  border: 5px solid #000;
  border-bottom: 2px solid #000;
  border-radius: 10%;
  border-top: 2px solid #000;
  color: #fff;
  font-size: 12px;
  height: 18px;
  padding: 0 0 3px;
  position: relative;
  top: 1px;
}

.nav-shrinked ul li:last-of-type a span {
  position: relative;
  top: 1px;
}

.nav-shrinked .sub-menu {
  display: none !important;
}

