/* Media Query - Max-Width: 690px
---------------------------------------------------------------------------------------------------- */

@media only screen and (max-width: 43.125em) {
  .featured-content .entry-header,
  .widget-area.footer-widget-area .featured-content .entry-header {
    max-width: 100%;
  }

  #topbar,
  #top,
  .site-inner,
  .footer-widgets .wrap,
  .site-footer,
  .site-footer .wrap {
    max-width: 100%;
  }

  .logo {
    max-width: 100%;
    width: 100%;
  }
  #topbar #tools {
    display: none;
  }
}
